.header {
  background-image: url(./components/Simple.png);
  width: 100%;
  height: 350px;
  color: white;
  display: flex;
  justify-content: space-around;
}

.container {
  margin: 10px auto;
  width: 80%;
  height: 100%;


}

.container p {
  font-size: 20px;
  font-weight: 600;
  margin: 10px auto;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.registration a {
  text-decoration: none;
  color: white;
  font-size: 20px;
  font-weight: 600;
  margin: 10px auto;
}

.sevice_container {
  display: flex;
  flex-direction: column;
}

.sevice_container h1 {
  font-size: 40px;
  font-weight: 800;
  margin: 10px auto;
}

.sevice_container .service {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.sevice_container .service .card {
  width: 30%;
  height: 250px;
  background-color: #f5f5f5;
  margin: 10px;
  border-radius: 10px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
}

.article_card:hover {
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
}

.footer {
  background-color: rgba(0, 0, 0, 0.726);
  width: 100%;
  color: white !important;
  display: flex;
  justify-content: space-between;
  align-items: center;
}